import React from "react";
import { connect } from "react-redux";

import { Aux } from "../../hoc/Auxiliary/Auxiliary";
import { Burger } from "../../components/Burger/Burger";
import { BuildControls } from "../../components/Burger/BuildControls/BuildControls";
import Modal from "../../components/UI/Modal/Modal";
import { BurgerSummary } from "../../components/Burger/BurgerSummary/BurgerSummary";
import axios from "../../axios-orders";
import Spinner from "../../components/UI/Spinner/Spinner";
import withErrorHandler from "../../hoc/withErrorHandler/withErrorHandler";
import * as actions from "../../store/actions/burgerBuilder";
import * as orderActions from "../../store/actions/order";

const BurgerBuilder = (props) => {
  // constructor(props) {
  //   super(props);
  //   state = {
  //     purchasing: false,
  //   }
  // }
  const [purchasing, setPurchasing] = React.useState(false);

  // componentDidMount() {
  // props.onInitIngredients();
  //   }
  const {onInitIngredients} = props
  React.useEffect(
    () => {
      onInitIngredients();
    },
    [onInitIngredients]
  );

  const updatePurchasable = (ingredients) => {
    let sum = Object.keys(ingredients)
      .map((ingr) => ingredients[ingr])
      .reduce((sum, el) => {
        return sum + el;
      }, 0);
    return sum > 0;
  };

  const purchaseHandler = () => {
    props.isAuthanticated ? setPurchasing(true) : props.history.push("/auth");
  };

  const purchaseContinueHandler = () => {
    props.history.push("/checkout");
    props.onOrderInit();
  };

  const purchaseCloseHandler = () => {
    setPurchasing(false);
  };

  const disabledInfo = {
    ...props.ingr,
  };

  for (let ingr in disabledInfo) {
    disabledInfo[ingr] = disabledInfo[ingr] <= 0;
  }
  let orderModal = null;

  let burger = props.error ? (
    <p style={{ color: "black", margin: "200px 0 0 50px" }}>
      ingredients can't be loaded
    </p>
  ) : (
    <Spinner />
  );
  if (props.ingr) {
    burger = (
      <Aux>
        <Burger ingredients={props.ingr} />
        <BuildControls
          ingredientsAdd={props.onIngredientsAdded}
          ingredientsRemove={props.onIngredientsRemoved}
          disabled={disabledInfo}
          purchasable={updatePurchasable(props.ingr)}
          price={props.price}
          ordered={purchaseHandler}
          isAuthanticated={props.isAuthanticated}
        />
      </Aux>
    );
    orderModal = (
      <BurgerSummary
        ingredients={props.ingr}
        continuePurchasing={purchaseContinueHandler}
        CancelPurchasing={purchaseCloseHandler}
        price={props.price}
      />
    );
  }

  return (
    <Aux>
      <Modal show={purchasing} close={purchaseCloseHandler}>
        {orderModal}
      </Modal>
      {burger}
    </Aux>
  );
};

const mapStateToProps = (state) => {
  return {
    ingr: state.burgerBuilder.ingredients,
    price: state.burgerBuilder.price,
    error: state.burgerBuilder.error,
    isAuthanticated: state.auth.token !== null,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onIngredientsAdded: (ingName) => dispatch(actions.addIngredient(ingName)),
    onIngredientsRemoved: (ingName) =>
      dispatch(actions.removeIngredient(ingName)),
    onInitIngredients: () => dispatch(actions.initIngredients()),
    onOrderInit: () => dispatch(orderActions.burgerOrderInit()),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(BurgerBuilder, axios));
